.place {
  &__photos {
    margin-bottom: 24px;
  }
  &__photo-container {
    position: relative;
    width: 100%;
    max-height: 256px;
    box-sizing: border-box;
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }


    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
  }
  &__description {
  }

  &__link {
    font-weight: 700;
    text-decoration: none;
    color: $colorBlue;
  }
}
